<template>
  <div class="Home Login">
    <div class="loginBox bg-dark-2">
        <div class="logo">
          <q-img src="@/assets/logos/lsc-logo-white.png"/>
        </div>
        <div class="header text-h6 font-normal font-primary text-center q-mt-md">Hoş geldiniz</div>

        <div class="inputs q-pt-lg">
          <q-input label="Kullanıcı Adı" v-model="username" dark/>
          <q-input type="password" label="Şifre" v-model="password" dark/>
        </div>

        <div class="buttons q-pt-lg row column">
          <q-toggle v-model="remember" color="secondary" label="Beni hatırla?" class="font-light font-primary" />
          <q-btn label="Giriş" push color="primary" text-color="dark" class="font-normal q-mt-xl" @click="login"/>
          
        </div>

        <div class="errorArea" v-if="error.show">
          <div :class="error.class + ' ' + error.color + ' q-px-md q-py-sm bg-dark-1 q-ma-md font-normal'">
            <div class="text text-center font-semibold">{{error.title}}</div>
            <div class="text-center text-primary">{{error.body}}</div>
          </div>
        </div>       
    </div>

    <div class="videoBox bg-dark-1">          
      <div class="text-h4 font-normal absolute-center font-primary">Video Alanı</div>
    </div>
    <!--<inventory />-->
  </div>
</template>

<script>
// @ is an alias to /src
//import inventory from '@/components/Gui/inventory/inventory.vue';


export default {
  name: 'Home',
  
  data: () => ({
    username: '',
    password: '',
    remember: false,
    error: {
      show: false,
      title: "HATA",
      body: 'Kullanıcı adı/Şifre hatalı',
      class: '',
      color: 'text-red',
    }
  }),

  components: {
    //inventory
  },

  methods: {
    login () {
      if(this.username.length <= 1 || this.password.length <= 1){
        if(this.error.show)
          return;
        else {
          this.error.title = "UYARI"
          this.error.body = "Kullanıcı Adı/Şifre boş olmamalıdır."
          this.error.color = "text-yellow"
          this.error.show = true

          setTimeout(() => {
            this.error.show = false
          }, 3000);
        }
      }
      else {
        if('alt' in window) {
          alt.emit('Plugin:Login:Submit', this.username, this.password, this.remember)
        }
        else {
          console.log('Login btn tıklandı.');
        }
      }
    },

    storedData(username, password) {
       if(username) { this.username = username; this.remember = true; }
       if(password) this.password = password;
    },

    loginResponseSucess() {      
      this.$router.push('/characters')      
    },

    loginResponseFailed() {
       this.error.title = "HATA"
        this.error.body = "Kullanıcı Adı/Şifre hatalı."
        this.error.color = "text-red"
        this.error.show = true

        setTimeout(() => {
          this.error.show = false
        }, 3000);
    },
  },

  mounted() {
    if('alt' in window){
      alt.emit('Plugin:Login:GetSavedData');
      alt.on('Plugin:Login:SetSavedData', this.storedData);
      alt.on('Plugin:Login:Response:Success', this.loginResponseSucess);
      alt.on('Plugin:Login:Response:Failed', this.loginResponseFailed);
    }
  }
}
</script>


