<script>
export default {
  mounted() {
    if('alt' in window) {
      alt.on('Model:Account:PlaceData', this.placeAccountData)
      alt.on('Model:Character:PlaceData', this.placeCharacterData)
    }
  },

  methods: {
    placeAccountData(account) {
      this.$store.state.account = account;
    },

    placeCharacterData(character) {
      this.$store.state.character = character;
    }
  }
}
</script>